:root {
  --color-main: #edeeee;
  --color-primary: #fefefc;
  --color-secondary: #cfada6;
}

.form-control,
.form-select {
  border: 1px solid var(--color-dark);
  box-shadow: none;
}

.couple-section .orn-2 {
  bottom: 20%;
}
.couple-section .orn-4 {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 180px;
  transform: translate(35%, 0) scaleX(-1);
}
@media (min-width: 768px) {
  .couple-section .orn-4 {
    width: 340px;
  }
}
@media (min-width: 992px) {
  .couple-section .orn-4 {
    width: 360px;
  }
}
.couple-section .orn-5 {
  position: absolute;
  top: 0;
  left: 0;
  width: 260px;
  transform: translate(-30%, 0);
}
@media (min-width: 768px) {
  .couple-section .orn-5 {
    width: 320px;
  }
}
@media (min-width: 992px) {
  .couple-section .orn-5 {
    width: 340px;
    transform: translate(-20%, 0);
  }
}
.couple-section .orn-6 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 240px;
  transform: translate(-40%, 0);
  z-index: 6;
}
@media (min-width: 768px) {
  .couple-section .orn-6 {
    width: 360px;
    transform: translate(-35%, 0);
  }
}
@media (min-width: 992px) {
  .couple-section .orn-6 {
    width: 420px;
  }
}
.couple-section .orn-7 {
  position: absolute;
  top: 0;
  right: 0;
  width: 240px;
  transform: translate(40%, 0) scale(-1);
}
@media (min-width: 768px) {
  .couple-section .orn-7 {
    width: 360px;
  }
}
@media (min-width: 992px) {
  .couple-section .orn-7 {
    width: 420px;
  }
}
@media (max-width: 991px) {
  .couple-section .couple-wrapper {
    padding-bottom: 3rem;
  }
}
.couple-section .couple .couple-description::before {
  color: #edeeee;
}
.couple-section .couple .couple-description::after {
  z-index: 8;
}
.couple-section .couple .couple-description * {
  z-index: 10;
}

.story-section {
